@import url(https://fonts.googleapis.com/css?family=Lora|Montserrat&display=swap);
.rory-about {
    font-family: 'Montserrat';
}

.about-me {
    font-family: 'Lora', serif;
    text-align: center;
    padding: 25px;
    /* background-color: rgb(76, 136, 141); */
    background-color: #364958;
    color: rgb(255, 255, 255);
    font-weight: 200;
}

.call {
    font-weight: 500;
    font-family: 'Montserrat';

}

.phone {
    font-weight: 500;
    font-family: 'Montserrat';
}
.portfolio-list {
    text-align: center;
}
.portfolio-item {
    display: inline-block;
    border: 2px solid rgb(22, 22, 22);
    border-radius: 10px;
    text-align: center;
    margin: 10px;
    /* background-color: lightgrey; */
    background-color: rgba(21, 121, 99, 0.089);
    position: relative;
    color: rgb(0, 0, 0);
}

.portfolio-item:hover {
    cursor: pointer;
}

.portfolio-item:hover .overlay {
    opacity: 0.9;
  }

.img {
    max-height: 200px;
    max-width: 200px;
    margin: 0 auto;
    padding: 1px;
    
}

.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
  }

  .text {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
  }

.item-title{
    font-weight: 300;
    padding-top: 10px;
    padding-bottom: 10px;
    
}

.item-code{
    padding-top: 10px;
    padding-bottom: 10px;
}

.item-desc{
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 12px;
}

.tech-list {
    margin-top: 5px;
}

.item-code {
    font-size: 20px;
    color: red;
    background-color: rgb(0, 0, 0);
    padding: 5px;
    border: 2px solid red;
    position: relative;
    top: 10px;
}

.item-code:hover {
    color: red;
    background-color: rgb(20, 20, 20);
}
.techItem {
    color: white;
    font-weight: 400;
    font-size: 14px;
}
.button-list {
    position: absolute;
    top: 20px;
    right: 20px;
}


.button:hover {
    cursor: pointer;
}

.fab {
    color: rgb(255, 255, 255);
    font-size: 26px;
}

.fab:hover {
    color: rgb(41, 172, 196);
}
.cv {
    color: rgb(255, 255, 255);
    font-weight: 600;
    font-size: 18px;

}

.cv:hover {
    color: rgb(41, 172, 196);
}
